body {
  margin: 0;
}

.App {
  text-align: center;
  min-height: 100vh;
}

li,
ul {
  padding: 0;
  margin: 0;
}

ul {
  list-style: none;
}
.a11y-hidden {
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
}
